.header-cart__link {
  position: relative;
  padding: 9px 11px 9px clamp(2.75rem, 2.556rem + 0.86vw, 3.25rem);
  font-weight: 700;
  font-size: 13px;
  color: var(--white);
  border-radius: 11px;
  background-color: var(--red);
  background-image: url("../images/icons/icon-cart.svg");
  background-position: left 11px center;
  background-repeat: no-repeat;
  background-size: clamp(1.375rem, 1.278rem + 0.43vw, 1.625rem) clamp(1.188rem, 1.115rem + 0.32vw, 1.375rem);
}
.header-cart__circle {
  position: absolute;
  top: 3px;
  left: 28px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 15px;
  height: 15px;
  font-size: 8px;
  color: var(--red);
  background-color: var(--yellow);
  border-radius: 100%;
}